import { NgModule } from '@angular/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { DynamicFormModule } from '../components/dynamic-form/dynamic-form.module';
import { MaterialModule } from '../material.module';
import { SharedFilterComponent } from './sharedfilter.component';


const BASE_MODULES = [];
const MATERIAL_MODULES = [MaterialModule, TeiTextTranslatePipeModule];
const TEI_MATERIAL_MODULES = [DynamicFormModule];

const COMPONENTS = [SharedFilterComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ]
})
export class SharedFilterModule { }
