import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { FieldConfig, FieldOption } from '../../field.interface';


@Component({
  selector: 'tei-material-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss'],
})
export class MenuComponent implements OnInit
{
  public field: FieldConfig;
  public group: UntypedFormGroup;

  constructor()
  {

  }

  ngOnInit()
  {

  }

  itemExpansion(value: FieldOption)
  {
    this.field.options.forEach(item =>
    {
      if (item.display !== value.display)
      {
        this.collapseRecursive(item);
      }
    });
  }

  collapseRecursive(item: FieldOption)
  {
    if (item)
    {
      item.expanded = false;
    }

    if (item.children)
    {
      item.children.forEach(it =>
      {
        this.collapseRecursive(it);
      });
    }
  }

}
