import { Component, OnInit, ViewChild } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { MatLegacyListOption as MatListOption, MatLegacySelectionListChange as MatSelectionListChange } from '@angular/material/legacy-list';
import { FieldConfig } from '../../field.interface';

@Component({
  selector: 'tei-material-selectionlist',
  templateUrl: './selectionlist.component.html',
  styleUrls: ['./selectionlist.component.scss']
})
export class SelectionListComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;
  @ViewChild('allSelected', { static: true }) private allSelected: MatListOption;
  constructor()
  {

  }

  ngOnInit()
  {
    if (this.group.get(this.field.name).value.length == this.field.options.length)
    {
      this.allSelected.selected = true;
    }
  }

  selectionChange(event: MatSelectionListChange)
  {
    const selectedOptions = event.options[0].value;
    if (selectedOptions === 0)
    {
      if (this.allSelected.selected)
      {
        this.group.get(this.field.name)
          .patchValue([...this.field.options.map(item => item.value), 0]);
      } else
      {
        this.group.get(this.field.name).patchValue([]);
      }
    }
    else
    {
      if (this.allSelected.selected)
      {
        this.allSelected.selected = false;
        return false;
      }
      if (this.group.get(this.field.name).value.length == this.field.options.length)
      {
        this.group.get(this.field.name).patchValue([...this.field.options.map(item => item.value), 0]);
      }
      else
      {
        this.group.get(this.field.name).patchValue(this.group.get(this.field.name).value);
      }

    }
  }

}
