import { Component, OnInit } from '@angular/core';
import { FieldConfig } from '../../field.interface';
import { UntypedFormGroup } from '@angular/forms';

@Component({
  selector: 'tei-material-chips',
  templateUrl: './chips.component.html',
  styleUrls: ['./chips.component.scss']
})
export class ChipsComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;
  constructor()
  {

  }

  ngOnInit()
  {

  }

  clearfilter()
  {
    this.group.get(this.field.name).reset();
    this.field.value = [];
   }

  toggleTag(item: FieldConfig)
  {
    var arrVal: Array<string> = this.field.value;
    if (arrVal.findIndex(x => x === item.value) == -1)
    {
      arrVal.push(item.value);
    }
    else
    {
      arrVal = arrVal.filter(x => x != item.value)
    }
    this.field.value = arrVal;
    this.group.get(this.field.name).patchValue(this.field.value);
   }
}
