import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DynamicFormModule } from './components/dynamic-form/dynamic-form.module';
import { SharedFilterModule } from './sharedfilter/sharedfilter.module';

const BASE_MODULES = [CommonModule];
const MODULES = [DynamicFormModule, SharedFilterModule]

const COMPONENTS = [];
const PIPES = [];

@NgModule({
  imports: [...BASE_MODULES, ...MODULES],
  exports: [...BASE_MODULES, ...MODULES, ...COMPONENTS, ...PIPES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: []
})
export class MaterialGenericFilterModule { }
