import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { MatDatepicker } from '@angular/material/datepicker';
import moment from "moment";
import { FieldConfig } from '../../field.interface';



@Component({
  selector: 'tei-material-date',
  templateUrl: './date.component.html',
  styleUrls: ['./date.component.scss']
})
export class DateComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;

  maxDate: moment.Moment = moment();
  minDate: moment.Moment = moment();

  todayDate: moment.Moment = moment();

  toggleValue: string = 'upcoming';
  constructor()
  {
    //this.alwaysShowCalendars = true;
  }

  ngOnInit()
  {
    //if (this.field.value1.endDate == null || Object.is(this.field.value1.endDate._i,NaN))
    if (this.field.value2 == null)
    {
      this.toggleValue = 'upcoming';
    }
    else
    {
      this.toggleValue = 'past';
    }
  }
  // opens = 'center';
  // drops = 'down';
  onValChange(event)
  {

    if (event.value === 'upcoming')
    {
      this.group.get(this.field.name).patchValue('upcoming');
      this.group.get(this.field.formgroupname).get(this.field.name1).patchValue(this.todayDate);
      this.group.get(this.field.formgroupname).get(this.field.name2).patchValue(null);
    }
    else
    {
      this.group.get(this.field.name).patchValue('past');
      this.group.get(this.field.formgroupname).get(this.field.name1).patchValue(moment().add(this.field.pastDateValue, 'days'));
      this.group.get(this.field.formgroupname).get(this.field.name2).patchValue(this.todayDate);
    }
  }

  openDatePicker(picker: MatDatepicker<any>)
  {
    picker.open();
  }
}
