import { NgxSliderModule } from '@angular-slider/ngx-slider';
import { CommonModule, DatePipe } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { RouterModule } from '@angular/router';
import { SafeHtmlPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { MaterialModule } from '../../material.module';
import { ButtonComponent } from '../button/button.component';
import { CheckboxComponent } from '../checkbox/checkbox.component';
import { ChildmenuComponent } from '../childmenu/childmenu.component';
import { ChipsComponent } from '../chips/chips.component';
import { DateComponent } from '../date/date.component';
import { DynamicFieldDirective } from '../dynamic-field/dynamic-field.directive';
import { InputComponent } from '../input/input.component';
import { MenuComponent } from '../menu/menu.component';
import { RadiobuttonComponent } from '../radiobutton/radiobutton.component';
import { SelectComponent } from '../select/select.component';
import { SelectionListComponent } from '../selectionlist/selectionlist.component';
import { SliderComponent } from '../slider/slider.component';
import { SlidetoggleComponent } from '../slidetoggle/slidetoggle.component';
import { TogglebuttonComponent } from '../togglebutton/togglebutton.component';
import { DynamicFormComponent } from './dynamic-form.component';
import { DynamicFormService } from './dynamic-form.service';
import { MatDividerModule } from '@angular/material/divider';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, SafeHtmlPipeModule];
const MATERIAL_MODULES = [RouterModule, MatDividerModule];
const CORE_MODULES = [TeiTextTranslatePipeModule];
const PIPES = [];
const PROVIDERS = [DynamicFormService, DatePipe];

const COMPONENTS = [
    InputComponent,
    ButtonComponent,
    SelectComponent,
    DateComponent,
    RadiobuttonComponent,
    CheckboxComponent,
    TogglebuttonComponent,
    SlidetoggleComponent,
    ChipsComponent,
    SelectionListComponent,
    MenuComponent,
    ChildmenuComponent,
    SliderComponent
];

@NgModule({
    declarations: [...COMPONENTS,
    ...PIPES,
        DynamicFieldDirective,
        DynamicFormComponent],
    imports: [...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...CORE_MODULES,
        MaterialModule,
        NgxSliderModule
    ],
    exports: [
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        // ...MODULES,
        ...COMPONENTS,
        ...PIPES,
        DynamicFieldDirective,
        DynamicFormComponent,
    ],
    providers: [...PROVIDERS]
})
export class DynamicFormModule { }
