import { animate, state, style, transition, trigger } from '@angular/animations';
import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Input, OnInit, Output, Renderer2, ViewChild } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { FieldConfig, FieldOption } from '../../field.interface';

@Component({
  selector: 'tei-material-childmenu',
  templateUrl: './childmenu.component.html',
  styleUrls: ['./childmenu.component.scss'],
  animations: [
    trigger('indicatorRotate', [
      state('collapsed', style({ transform: 'rotate(0deg)' })),
      state('expanded', style({ transform: 'rotate(180deg)' })),
      transition('expanded <=> collapsed',
        animate('225ms cubic-bezier(0.4,0.0,0.2,1)')
      ),
    ])
  ]
})
export class ChildmenuComponent implements OnInit
{
  @Input() group: UntypedFormGroup;
  @Input() field: FieldConfig;
  @Input() item: FieldOption;
  @Input() depth: number;
  @Output() itemExpanded = new EventEmitter<FieldOption>();
  isSelected: boolean = false;
  @ViewChild('menulist') menulist: ElementRef;

  constructor(private renderer: Renderer2)
  {
    if (this.depth === undefined)
    {
      this.depth = 0;
    }
  }

  ngOnInit()
  {
  }

  onItemSelected(item: FieldOption)
  {
    const c = document.getElementsByClassName('active-mat-list-item');
    if (c.length > 0)
    {
      c[0]['classList'].remove('active-mat-list-item');
    }
    this.menulist["_element"].nativeElement.classList.add('active-mat-list-item');

    this.itemExpanded.emit(item);

    if (item.children && item.children.length)
    {
      this.item.expanded = !this.item.expanded;
    }
    this.group.get(this.field.name).patchValue(item.value);
  }
}
