import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { DynamicFormComponent } from '../components/dynamic-form/dynamic-form.component';
import { DynamicFormService } from '../components/dynamic-form/dynamic-form.service';
import { FieldConfig } from '../field.interface';


@Component({
  selector: 'tei-material-sharedfilter',
  templateUrl: './sharedfilter.component.html',
  styleUrls: ['./sharedfilter.component.scss']
})
export class SharedFilterComponent implements OnInit
{
  @Input() InputFilterData: FieldConfig[] = [];
  @Input() view: string = 'md';
  @Output() filterEmitterparent = new EventEmitter<any>();
  filters = [];

  constructor(public dialog: MatDialog,
    private dynamicfilter: DynamicFormService)
  {

  }

  ngOnInit()
  {
    this.dynamicfilter.currentFilter.subscribe(filterValue =>
    {
      this.filters = filterValue;
    });
  }

  openDialog(): void
  {
    this.InputFilterData.forEach(x =>
    {
      if (this.filters)
      {
        x.value = this.filters[x.name];
      }
      if (x.type === "date")
      {
        if (this.filters)
        {
          let formgroup = this?.filters[x.formgroupname];
          x.value1 = formgroup[x.name1];
          x.value2 = formgroup[x.name2];
        }
      }
    });


    let dialogRef = this.dialog.open(DynamicFormComponent, {
      panelClass: ['o-dialog-container', 'o-dialog-sm-fullscreen'],
      data: { filterdata: this.InputFilterData }
    });
  }
}
