import { Component, EventEmitter, Input, OnChanges, OnInit, Output, Inject } from '@angular/core';
import { FieldConfig, Validator } from '../../field.interface';
import { UntypedFormGroup, UntypedFormBuilder, Validators, FormControl } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { DynamicFormService } from './dynamic-form.service';
import { Subscription } from 'rxjs';
import moment from "moment";
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ResponsiveService } from '@tei/core';


@Component({
  exportAs: "tei-material-dynamic-form",
  selector: 'tei-material-dynamic-form',
  templateUrl: './dynamic-form.component.html',
  styleUrls: ['./dynamic-form.component.scss']
})
export class DynamicFormComponent implements OnInit
{
  @Input() fields: FieldConfig[] = [];
  @Output() submit: EventEmitter<any> = new EventEmitter<any>();
  form: UntypedFormGroup;
  dategrouprange: UntypedFormGroup;
  formValues: any[] = [];
  showSpinner: boolean;
  overlay: string;
  isMobile = false;
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
   private dialogRef: MatDialogRef<DynamicFormComponent>,
   private fb: UntypedFormBuilder,
   private dynamicfilter: DynamicFormService,
  private responsiveService: ResponsiveService)
  {

  }

  ngOnInit()
  {
     this.getMobileSize();
    this.fields = (this.data && this.data.filterdata) ? this.data.filterdata : this.fields;
    this.form = this.createControl();
    this.form.valueChanges.subscribe((value) =>
    {

      this.formValues.push(value);
      setTimeout(() =>
      {
        if (this.formValues.length >= 4)
        {
          if (this.formValues.length == 9)
          {
            this.dynamicfilter.emitValue(this.formValues[8]); //emits date event with past and upcoming toggle
            this.formValues = [];
          }
          else
          {
            this.dynamicfilter.emitValue(this.formValues[3]); //emits date event
            this.formValues = [];
          }
        }
        if (this.formValues.length == 2)   //emits all the other controls
        {
          this.dynamicfilter.emitValue(this.formValues[1]);
          this.formValues = [];
        }
        if (this.formValues.length == 1)
        {
          this.dynamicfilter.emitValue(this.formValues[0]);
          this.formValues = [];
        }
      }, 100);
    });
    this.dynamicfilter.visibility.subscribe(spinnervalue =>
    {
      this.showSpinner = spinnervalue;
      (spinnervalue) ? this.overlay = "dark-overlay" : "";
    });
  }

getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm';
  }

  onSubmit(event: Event)
  {
    event.preventDefault();
    event.stopPropagation();
    if (this.form.valid)
    {
      this.submit.emit(this.form.value);
    } else
    {
      this.validateAllFormFields(this.form);
    }
    this.dialogRef.close();
  }

  createControl()
  {
    const group = this.fb.group({});
    const nestgroup = this.fb.group({});
    this.fields.forEach(field =>
    {
      if (field.type === "button") return;
      const control = this.fb.control(
        field.value,
        this.bindValidations(field.validations || [])
      );
      if (field.name)
      {
        group.addControl(field.name, control);
      }
      if (field.type === "date")
      {
        const control1 = this.fb.control(
          field.value1,
          this.bindValidations(field.validations || [])
        );
        nestgroup.addControl(field.name1, control1)
        const control2 = this.fb.control(
          field.value2,
          this.bindValidations(field.validations || [])
        );
        nestgroup.addControl(field.name2, control2)
        group.addControl(field.formgroupname, nestgroup)
      }
      if (field.type === "slider")
      {
        const slidercontrol1 = this.fb.control(
          field.value1,
          this.bindValidations(field.validations || [])
        );
        group.addControl(field.name1, slidercontrol1)
      }
    });
    return group;
  }

  bindValidations(validations: any)
  {
    if (validations.length > 0)
    {
      const validList = [];
      validations.forEach(valid =>
      {
        validList.push(valid.validator);
      });
      return Validators.compose(validList);
    }
    return null;
  }

  validateAllFormFields(formGroup: UntypedFormGroup)
  {
    Object.keys(formGroup.controls).forEach(field =>
    {
      const control = formGroup.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }

  onClose() {
    this.dialogRef.close();
  }
}
